.coverImg {
    border-radius: 50%;
    width: 450px;
    height: 450px;
    margin-top: 100px;
    overflow: hidden; 
    object-fit: contain;
}

.coverImg img {
    -webkit-filter: sepia(120%) hue-rotate(190deg) saturate(400%) grayscale(10%) opacity(90%);
            filter: sepia(120%) hue-rotate(190deg) saturate(400%) grayscale(10%) opacity(90%);
    background-blend-mode: multiply;
    object-fit: contain;
    height: 100%;
    width: 100%; 
    transition: box-shadow 0.3s, -webkit-filter 0.3s; 
    transition: filter 0.3s, box-shadow 0.3s; 
    transition: filter 0.3s, box-shadow 0.3s, -webkit-filter 0.3s;
    -webkit-transition: filter 0.3s, -webkit-filter 0.3s, box-shadow 0.3s; 
}

.coverImg img:hover {
    -webkit-filter: grayscale(100%) 
    brightness(135%)
    contrast(120%);
    filter: grayscale(100%) 
    brightness(135%)
    contrast(120%);
    transition: box-shadow 0.3s, -webkit-filter 0.3s;
    transition: filter 0.3s, box-shadow 0.3s;
    transition: filter 0.3s, box-shadow 0.3s, -webkit-filter 0.3s;
    -webkit-transition: filter 0.3s, -webkit-filter 0.3s, box-shadow 0.3s;
}

